
  import { defineComponent, ref, onMounted } from "vue";

  export default defineComponent({
    emits: ["onAddItem", "onDeleteChecked", "onResetChecked", "onClearList"],
    props: ["phrase"],

    setup(props, { emit }) {
      const input = ref();

      onMounted(() => {
        input.value.placeholder = props.phrase;
      });

      function resetInput() {
        input.value.value = "";
        input.value.focus();
      }

      function emitOnAddItem() {
        emit("onAddItem", input.value.value);
        resetInput();
      }
      function emitOnDeleteChecked() {
        emit("onDeleteChecked");
      }
      function emitOnResetChecked() {
        emit("onResetChecked");
      }
      function emitOnClearList() {
        emit("onClearList");
      }
      return {
        input,
        resetInput,
        emitOnAddItem,
        emitOnDeleteChecked,
        emitOnResetChecked,
        emitOnClearList,
      };
    },
  });
